.about {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.face {
  width: 30%;
  margin-left: 10%;
}

.face img {
  width: 100%;
}

.bio {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 40%;
  margin-right: 10%;
}

.bio p {
  margin-bottom: 0;
}

.bio .links ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.bio .links ul li {
  display: inline-block;
  margin-right: 1.5rem;
}

.bio .links ul li:last-child {
  margin-right: 0;
}

@media only screen and (max-width: 767px) {
  .about {
    padding: 0 2rem;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .face {
    width: 60%;
    margin: 0;
    margin-bottom: 3rem;
  }

  .bio {
    width: 100%;
    text-align: center;
    margin-left: 0;
    margin-right: 0;
  }
}

footer {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 1rem;
}



